
import store from "@/store";
import { Options, Vue } from "vue-class-component";
import graphList from "@/graph/list";
import Dialog from "@/components/Dialog.vue";
import {
  hideLoader,
  importCSVFromURL,
  importFromCSV,
  showLoader,
} from "@/tools";
interface ToolbarItem {
  icon?: string;
  name?: string;
  group?: boolean;
  action?(): void;
  children?: ToolbarItem[];
  expanded?: boolean;
}

@Options({
  components: { Dialog },
})
export default class Header extends Vue {
  pickURLDialog = false;
  importURL = "";

  importFromFile(): void {
    const fileUpload: HTMLInputElement = document.getElementById(
      "fileUpload"
    ) as HTMLInputElement;
    const files = fileUpload.files;
    if (!files || files.length < 1) return alert("No files selected");
    const file = files[0];
    const reader = new FileReader();
    showLoader("Loading File");
    reader.readAsText(file);
    reader.onload = (event: ProgressEvent<FileReader>) => {
      if (!event.target) return alert("Failed to load file!");
      var content = event.target.result as string;
      importFromCSV(content);
      const rows = store.state.rows;
      alert(`Imported ${rows.length}row(s)`);
      hideLoader();
    };
  }

  importFromURL(): void {
    var regex = /^(ftp|http|https):\/\/[^ "]+$/;
    if (this.importURL.length < 1) return alert("No URL provided!");
    if (!regex.test(this.importURL)) return alert("Invalid url provided");
    this.pickURLDialog = false;
    importCSVFromURL(this.importURL)
      .then(() => {
        this.importURL = "";
        const rows = store.state.rows;
        alert(`Imported ${rows.length}row(s)`);
      })
      .catch(() => alert("Failed to import csv"));
  }

  expanded = false;

  graphs = graphList;

  set graphType(value: string) {
    store.state.graph.type = value;
  }
  get graphType(): string {
    return store.state.graph.type;
  }

  set title(value: string) {
    store.state.graph.title = value;
  }

  get title(): string {
    return store.state.graph.title;
  }

  toolbar: ToolbarItem[] = [
    {
      name: "Data",
      children: [
        {
          icon: "file_upload",
          name: "Open File",
          action: () => {
            const fileUpload: HTMLInputElement = document.getElementById(
              "fileUpload"
            ) as HTMLInputElement;
            fileUpload.click();
          },
        },
        {
          icon: "content_paste",
          name: "Import from clipboard",
          action: () => {
            if ("navigator" in window) {
              showLoader("Loading Clipboard Contents");
              navigator.clipboard
                .readText()
                .then((text) => {
                  hideLoader();
                  importFromCSV(text);
                })
                .catch((err) => {
                  hideLoader();
                  alert("Unable to read contents from clipboard: " + err);
                });
            }
          },
        },
        { icon: "table_chart", name: "Paste Table (Legacy)" },
        {
          icon: "link",
          name: "Import from URL",
          action: () => (this.pickURLDialog = true),
        },
        { icon: "highlight_alt", name: "Select & Copy Data Table" },
        { icon: "file_download", name: "Download Data" },
        { icon: "save_alt", name: "Save Session" },
      ],
    },
    {
      name: "Edit",
      children: [
        { name: "Remove Row" },
        { name: "Remove Specific Row" },
        { name: "Remove Last Row" },
        {},
        { name: "Remove Column" },
        { name: "Remove Specific Column" },
        { name: "Remove Last Column" },
      ],
    },
    {
      name: "Insert",
      children: [
        {
          name: "Insert Row",
          action(): void {
            store.state.rows.push(new Array(store.state.cols.length));
          },
        },
        {
          name: "Insert Column",
          action(): void {
            store.state.cols.push("");
          },
        },
      ],
    },
  ];

  expand(item: ToolbarItem): void {
    item.expanded = true;
    this.expanded = true;
  }

  closeAll(): void {
    this.expanded = false;
    for (const other of this.toolbar) {
      other.expanded = false;
    }
  }

  action(item: ToolbarItem): void {
    if (item.action) {
      item.action();
      this.closeAll();
    }
  }
}
